.sc-btn {
  display: inline-block;
  position: relative;
  margin: 0 0.25em 0.5em 0;
  padding: 0;
  color: #fff;
  font-family: "Helvetica Neue", "Helvetica", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1em;
  text-decoration: none;
  text-shadow: rgba(0, 0, 0, 0.3) 0px -0.1em 0px;
  border: 0;
  border-radius: 0.4em;
  -webkit-border-radius: 0.4em;
  -moz-border-radius: 0.4em;
  -ms-border-radius: 0.4em;
  -o-border-radius: 0.4em;
  background-color: #1a1a1a;
  background-image: -webkit-linear-gradient(to bottom, #595959, #1a1a1a 100%);
  background-image: -o-linear-gradient(to bottom, #595959, #1a1a1a);
  background-image: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(#595959),
    to(#1a1a1a)
  );
  background-image: -webkit-linear-gradient(top, #595959, #1a1a1a);
  background-image: -o-linear-gradient(top, #595959, #1a1a1a);
  background-image: linear-gradient(to bottom, #595959, #1a1a1a);
  -webkit-box-shadow: inset rgba(0, 0, 0, 0.1) 0px -0.15em 0px,
    inset rgba(255, 255, 255, 0.2) 0px 0.15em 0px,
    rgba(0, 0, 0, 0.3) 0px 0.1em 0.3em;
  box-shadow: inset rgba(0, 0, 0, 0.1) 0px -0.15em 0px,
    inset rgba(255, 255, 255, 0.2) 0px 0.15em 0px,
    rgba(0, 0, 0, 0.3) 0px 0.1em 0.3em;
  text-align: center;
  background-repeat: no-repeat;
  -o-transition: background-position 0.1s ease-in-out;
  transition: background-position 0.1s ease-in-out;
  -webkit-transition: background-position 0.1s ease-in-out;
  -webkit-appearance: none;
  cursor: pointer;
  overflow: hidden;
}
.sc-btn:hover {
  color: #fff;
}
.sc-btn:active {
  -webkit-box-shadow: rgba(255, 255, 255, 0.2) 0 0.1em 0,
    inset rgba(0, 0, 0, 0.3) 0px 0.25em 1em;
  box-shadow: rgba(255, 255, 255, 0.2) 0 0.1em 0,
    inset rgba(0, 0, 0, 0.3) 0px 0.25em 1em;
}

.sc-icon,
.sc-text {
  display: block;
  float: left;
}

.sc-icon {
  margin: 0 -0.4em 0 0;
  padding: 0.6em 0.8em 0.5em;
  border-right: rgba(255, 255, 255, 0.1) 0.1em solid;
  -webkit-box-shadow: inset rgba(0, 0, 0, 0.1) -0.1em 0px 0px;
  box-shadow: inset rgba(0, 0, 0, 0.1) -0.1em 0px 0px;
}

.sc-text {
  padding: 0.95em 1em 0.85em 1em;
  font-size: 1.15em;
  text-align: center;
}

.sc-icon svg {
  width: 1.8em;
  height: 1.8em;
  fill: #fff;
}

.sc--flat {
  -webkit-box-shadow: none;
  box-shadow: none;
  background-image: none !important;
}
.sc--flat .sc-icon {
  border-color: transparent;
}
.sc--flat:active {
  -webkit-box-shadow: inset rgba(0, 0, 0, 0.3) 0px 0.15em 0.25em;
  box-shadow: inset rgba(0, 0, 0, 0.3) 0px 0.15em 0.25em;
}

.sc--default {
  color: #222;
  text-shadow: rgba(255, 255, 255, 0.4) 0 0.1em 0;
  background-color: #ebebeb;
  background-image: -webkit-linear-gradient(to bottom, white, #ebebeb 100%);
  background-image: -o-linear-gradient(to bottom, white, #ebebeb);
